*, :after, :before {
  box-sizing: border-box;
}

html {
  color: #060b35;
  font-family: Roboto, Arial, Helvetica, sans-serif;
  font-size: 10px;
  font-weight: 400;
  line-height: 1.4;
}

body, h2, p {
  margin: 0;
}

.main-header {
  justify-content: space-between;
  align-items: center;
  width: 100%;
  padding: 0 50px;
  display: flex;
  box-shadow: 0 2px 4px #0000000d;
}

.main-header__logo-img {
  display: block;
}

.main-nav__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.main-nav__item:not(:last-child) {
  margin-right: 20px;
}

.main-nav__link {
  color: inherit;
  text-transform: uppercase;
  align-items: center;
  height: 60px;
  font-size: 12px;
  font-weight: 500;
  line-height: 14px;
  text-decoration: none;
  display: flex;
}

.main-nav__link:hover {
  color: #00acdc;
}

.is-active {
  color: #00acdc;
  position: relative;
}

.is-active:after {
  content: "";
  background-color: #00acdc;
  border-radius: 8px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
}

.stars {
  display: flex;
}

.stars__star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
}

.stars__star:not(:last-child) {
  margin-right: 4px;
}

.stars--1 .stars__star:nth-child(-n+1), .stars--2 .stars__star:nth-child(-n+2), .stars--3 .stars__star:nth-child(-n+3), .stars--4 .stars__star:nth-child(-n+4), .stars--5 .stars__star:nth-child(-n+5) {
  background-image: url("star-active.edda4c43.svg");
}

.product-card {
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  flex-direction: column;
  width: 200px;
  padding: 32px 16px 16px;
  display: flex;
}

.product-card__img {
  text-align: center;
  width: 160px;
  margin: 0 auto 40px;
}

.product-card__title {
  margin-bottom: 4px;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.product-card__rating {
  justify-content: space-between;
  align-items: center;
  gap: 5px;
  margin-bottom: 24px;
  display: flex;
}

.product-card__reviews {
  line-height: 16px;
}

.product-card__code {
  color: #616070;
  margin-bottom: 16px;
}

.product-card__price {
  justify-content: space-between;
  align-items: center;
  gap: 5px;
  margin-bottom: 16px;
  display: flex;
}

.product-card__price-text {
  color: #616070;
  font-size: 12px;
}

.product-card__price-value {
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.product-card__link {
  text-transform: uppercase;
  color: #fff;
  background-color: #00acdc;
  border-radius: 5px;
  justify-content: center;
  align-self: stretch;
  align-items: center;
  padding: 12px;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
  text-decoration: none;
  display: flex;
}

.product-card__link:hover {
  color: #00acdc;
  background-color: #fff;
  border: 1px solid #00acdc;
}

.main__content {
  grid-template-columns: 1fr;
  justify-items: center;
  gap: 46px 48px;
  max-width: 1024px;
  margin: 0 auto;
  padding: 50px 40px;
  display: grid;
}

@media (width >= 488px) {
  .main__content {
    grid-template-columns: repeat(2, 1fr);
  }
}

@media (width >= 768px) {
  .main__content {
    grid-template-columns: repeat(3, 1fr);
  }
}

@media (width >= 1024px) {
  .main__content {
    grid-template-columns: repeat(4, 1fr);
  }
}

